import React, { useState, useEffect } from 'react';
import { api } from '../services/api';
import './DashboardPage.css';

interface InvoiceInfo {
  invoice_number: string;
  partner_name: string;
  amount_total: string;
  issue_date?: string;
  due_date?: string;
}

interface DashboardStats {
  invoices: {
    unpaid_sales: { 
      count: number; 
      total: string;
      oldest_invoices?: InvoiceInfo[];
    };
    unpaid_purchase: { 
      count: number; 
      total: string;
      oldest_invoices?: InvoiceInfo[];
    };
    overdue_sales: {
      count: number;
      oldest_invoices?: InvoiceInfo[];
    };
    overdue_purchase: {
      count: number;
      oldest_invoices?: InvoiceInfo[];
    };
  };
  orders: {
    finished: number;
    unfinished: number;
    new: number;
  };
  clients: {
    new_this_month: number;
  };
}

const DashboardPage: React.FC = () => {
  const [stats, setStats] = useState<DashboardStats | null>(null);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);

  useEffect(() => {
    const fetchStats = async () => {
      try {
        setLoading(true);
        const response = await api.get('/dashboard/statistics/');
        setStats(response.data);
        setError(null);
      } catch (err: any) {
        setError(err.response?.data?.error || 'Klaida ukraunant statistik');
        console.error('Dashboard statistics error:', err);
      } finally {
        setLoading(false);
      }
    };

    fetchStats();
  }, []);

  const formatCurrency = (amount: string) => {
    const num = parseFloat(amount);
    return new Intl.NumberFormat('lt-LT', {
      style: 'currency',
      currency: 'EUR',
      minimumFractionDigits: 2,
    }).format(num);
  };

  return (
    <div className="dashboard">
      <div className="container">
        <h1 style={{ marginBottom: '15px', color: '#333', fontSize: '20px' }}>Pagrindinis</h1>
        
        {loading && (
          <div style={{ textAlign: 'center', padding: '40px', color: '#666' }}>
            Kraunama...
          </div>
        )}

        {error && (
          <div style={{ 
            padding: '15px', 
            backgroundColor: '#fee', 
            color: '#c33', 
            borderRadius: '4px', 
            marginBottom: '20px' 
          }}>
            {error}
          </div>
        )}

        {stats && !loading && (
          <div className="dashboard-stats">
            {/* Sskait statistika */}
            <div className="stats-section">
              <h2>Sskaitos</h2>
              <div className="stats-grid">
                <div className="stat-card">
                  <div className="stat-label">Neapmoktos iraytos</div>
                  <div className="stat-value">{stats.invoices.unpaid_sales.count}</div>
                  <div className="stat-amount">{formatCurrency(stats.invoices.unpaid_sales.total)}</div>
                  {stats.invoices.unpaid_sales.oldest_invoices && stats.invoices.unpaid_sales.oldest_invoices.length > 0 && (
                    <div className="stat-invoices-list">
                      {stats.invoices.unpaid_sales.oldest_invoices.map((inv, idx) => (
                        <div key={idx} className="stat-invoice-item">
                          <div className="stat-invoice-number">{inv.invoice_number}</div>
                          <div className="stat-invoice-partner">{inv.partner_name}</div>
                        </div>
                      ))}
                    </div>
                  )}
                </div>
                <div className="stat-card">
                  <div className="stat-label">Neapmoktos gautos</div>
                  <div className="stat-value">{stats.invoices.unpaid_purchase.count}</div>
                  <div className="stat-amount">{formatCurrency(stats.invoices.unpaid_purchase.total)}</div>
                  {stats.invoices.unpaid_purchase.oldest_invoices && stats.invoices.unpaid_purchase.oldest_invoices.length > 0 && (
                    <div className="stat-invoices-list">
                      {stats.invoices.unpaid_purchase.oldest_invoices.map((inv, idx) => (
                        <div key={idx} className="stat-invoice-item">
                          <div className="stat-invoice-number">{inv.invoice_number}</div>
                          <div className="stat-invoice-partner">{inv.partner_name}</div>
                        </div>
                      ))}
                    </div>
                  )}
                </div>
                <div className="stat-card stat-card-warning">
                  <div className="stat-label">Vluojanios iraytos</div>
                  <div className="stat-value">{typeof stats.invoices.overdue_sales === 'object' ? stats.invoices.overdue_sales.count : stats.invoices.overdue_sales}</div>
                  {typeof stats.invoices.overdue_sales === 'object' && stats.invoices.overdue_sales.oldest_invoices && stats.invoices.overdue_sales.oldest_invoices.length > 0 && (
                    <div className="stat-invoices-list">
                      {stats.invoices.overdue_sales.oldest_invoices.map((inv, idx) => (
                        <div key={idx} className="stat-invoice-item">
                          <div className="stat-invoice-number">{inv.invoice_number}</div>
                          <div className="stat-invoice-partner">{inv.partner_name}</div>
                        </div>
                      ))}
                    </div>
                  )}
                </div>
                <div className="stat-card stat-card-warning">
                  <div className="stat-label">Vluojanios gautos</div>
                  <div className="stat-value">{typeof stats.invoices.overdue_purchase === 'object' ? stats.invoices.overdue_purchase.count : stats.invoices.overdue_purchase}</div>
                  {typeof stats.invoices.overdue_purchase === 'object' && stats.invoices.overdue_purchase.oldest_invoices && stats.invoices.overdue_purchase.oldest_invoices.length > 0 && (
                    <div className="stat-invoices-list">
                      {stats.invoices.overdue_purchase.oldest_invoices.map((inv, idx) => (
                        <div key={idx} className="stat-invoice-item">
                          <div className="stat-invoice-number">{inv.invoice_number}</div>
                          <div className="stat-invoice-partner">{inv.partner_name}</div>
                        </div>
                      ))}
                    </div>
                  )}
                </div>
              </div>
            </div>

            {/* Usakym statistika */}
            <div className="stats-section">
              <h2>Usakymai (is mnuo)</h2>
              <div className="stats-grid">
                <div className="stat-card stat-card-success">
                  <div className="stat-label">Baigti</div>
                  <div className="stat-value">{stats.orders.finished}</div>
                </div>
                <div className="stat-card stat-card-info">
                  <div className="stat-label">Nebaigti</div>
                  <div className="stat-value">{stats.orders.unfinished}</div>
                </div>
                <div className="stat-card stat-card-new">
                  <div className="stat-label">Nauji</div>
                  <div className="stat-value">{stats.orders.new}</div>
                </div>
              </div>
            </div>

            {/* Klient statistika */}
            <div className="stats-section">
              <h2>Klientai</h2>
              <div className="stats-grid">
                <div className="stat-card stat-card-new">
                  <div className="stat-label">Nauji  mnes</div>
                  <div className="stat-value">{stats.clients.new_this_month}</div>
                </div>
              </div>
            </div>
          </div>
        )}
      </div>
    </div>
  );
};

export default DashboardPage;

