import React from 'react';
import { Link, useLocation } from 'react-router-dom';
import { useAuth } from '../context/AuthContext';
import './Navigation.css';

const Navigation: React.FC = () => {
  const { logout } = useAuth();
  const location = useLocation();

  // Nerodome navigacijos login puslapyje
  if (location.pathname === '/login') {
    return null;
  }

  return (
    <nav className="navigation">
      <div className="nav-container">
        <Link to="/" className="nav-logo">
          TMS
        </Link>
        
        <div className="nav-links">
          <Link 
            to="/" 
            className={`nav-link ${location.pathname === '/' ? 'active' : ''}`}
          >
            Pagrindinis
          </Link>
          <Link 
            to="/orders" 
            className={`nav-link ${location.pathname === '/orders' ? 'active' : ''}`}
          >
            Usakymai
          </Link>
          <Link 
            to="/invoices" 
            className={`nav-link ${location.pathname === '/invoices' ? 'active' : ''}`}
          >
            Sskaitos
          </Link>
          <Link 
            to="/partners" 
            className={`nav-link ${location.pathname === '/partners' ? 'active' : ''}`}
          >
            Partneriai
          </Link>
          <Link 
            to="/settings" 
            className={`nav-link ${location.pathname === '/settings' || location.pathname === '/bank-import' ? 'active' : ''}`}
          >
            Nustatymai
          </Link>
        </div>

        <button onClick={logout} className="nav-logout">
          Atsijungti
        </button>
      </div>
    </nav>
  );
};

export default Navigation;

