  // Handle URL parameters for quick invoice creation
  useEffect(() => {
    const params = new URLSearchParams(location.search);
    
    // Patikrinti ar reikia atidaryti pardavimo sskait
    const salesId = params.get('sales_id');
    if (salesId) {
      setActiveTab('sales');
      api.get(`/invoices/sales/${salesId}/`).then(response => {
        const invoice = response.data;
        setSelectedSalesInvoice(invoice);
        setShowSalesDetails(true);
      }).catch(err => {
        console.error('Klaida ukraunant pardavimo sskait:', err);
        alert('Klaida ukraunant pardavimo sskait');
      });
      // Ivalyti URL
      navigate('/invoices', { replace: true });
      return;
    }
    
    // Patikrinti ar reikia atidaryti pirkimo sskait
    const purchaseId = params.get('purchase_id');
    if (purchaseId) {
      setActiveTab('purchase');
      api.get(`/invoices/purchase/${purchaseId}/`).then(response => {
        const invoice = response.data;
        setSelectedPurchaseInvoice(invoice);
        setShowPurchaseDetails(true);
      }).catch(err => {
        console.error('Klaida ukraunant pirkimo sskait:', err);
        alert('Klaida ukraunant pirkimo sskait');
      });
      // Ivalyti URL
      navigate('/invoices', { replace: true });
      return;
    }
    
    // Patikrinti ar reikia atidaryti pirkimo sskaitos form i usakymo
    if (params.get('create_purchase') === 'true') {
      const orderCarrierId = params.get('order_carrier_id');
      const orderId = params.get('order_id');
      const partnerId = params.get('partner_id');
      const amountNet = params.get('amount_net');
      
      if (orderCarrierId && partnerId) {
        setActiveTab('purchase');
        setPurchaseFormData(prev => ({
          ...prev,
          partner_id: partnerId,
          related_order_id: orderId || '',
          order_carrier_id: orderCarrierId,
          amount_net: amountNet || '',
          received_date: new Date().toISOString().split('T')[0],
        }));
        
        // Ukrauti tiekjo duomenis
        if (partnerId) {
          api.get(`/partners/partners/${partnerId}/`).then(response => {
            setSelectedSupplierName(response.data.name);
            setSupplierSearch(response.data.name);
          }).catch(err => console.error('Klaida ukraunant tiekj:', err));
        }
        
        // Ukrauti OrderCarrier duomenis
        if (orderCarrierId) {
          api.get(`/orders/carriers/${orderCarrierId}/`).then(response => {
            const carrier = response.data;
            setPurchaseFormData(prev => ({
              ...prev,
              amount_net: carrier.price_net ? String(carrier.price_net) : prev.amount_net,
              issue_date: carrier.loading_date ? (carrier.loading_date.includes('T') ? carrier.loading_date.split('T')[0] : carrier.loading_date) : prev.issue_date,
            }));
          }).catch(err => console.error('Klaida ukraunant OrderCarrier:', err));
        }
        
        setShowPurchaseForm(true);
        
        // Ivalyti URL
        navigate('/invoices', { replace: true });
      }
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [location.search, navigate]);
  
